// Buttons
.btn {
    &-rounded {
        box-shadow: 0 2px 12px 0 rgba(0,0,0,0.25);
        color: $mm-color-white;
        border: none;
        border-radius: 150px;
        width: $button-add-dimension;
        height: $button-add-dimension;
        cursor: pointer;
    }

    &-add {
        position: fixed;
        right: em-calc(10px);
        bottom: em-calc(10px);
        &:hover {
            transform: scale(1.1);
            transition: all 0.2s ease-in-out;
        }
    }
}
